<template>
  <div class="login_form">
    <div class="login_right">
      <Form ref="formInline" :model="formInline" :rules="ruleInline" inline>
        <div class="login_title">{{ $t("uc.login.login") }}</div>
        <FormItem prop="user">
          <Input
            name="user"
            type="text"
            v-model="formInline.user"
            :placeholder="$t('uc.login.usertip')"
            class="user"
          >
          </Input>
        </FormItem>
        <FormItem prop="password" class="password">
          <Input
            type="password"
            v-model="formInline.password"
            :placeholder="$t('uc.login.pwdtip')"
            @on-keyup="onKeyup"
          >
          </Input>
        </FormItem>
        <p id="notice" class="hide">{{ $t("uc.login.validatemsg") }}</p>
        <p style="height: 30px">
          <!-- 先關閉忘記密碼 -->
          <!-- <router-link
            to="/findPwd"
            style="
              color: #979797;
              float: right;
              padding-right: 10px;
              font-size: 12px;
            "
          >
            {{ $t("uc.login.forget") }}
          </router-link> -->
        </p>
        <FormItem style="margin-bottom: 15px">
          <Button class="login_btn" @click="login()">{{
            $t("uc.login.login")
          }}</Button>
        </FormItem>
        <div class="to_register">
          <span>{{ $t("uc.login.noaccount") }}</span>
          <router-link to="/register">{{
            $t("uc.login.goregister")
          }}</router-link>
        </div>
      </Form>
    </div>
  </div>
</template>
<script>
import gtInit from "../../assets/js/gt.js";
import $ from "jquery";
import { login } from "@/service/getData";
export default {
  data() {
    return {
      country: "+86",
      captchaObj: null,
      _captchaResult: null,
      formInline: {
        user: "",
        password: "",
      },
      ruleInline: {
        user: [
          {
            required: true,
            message: this.$t("uc.login.loginvalidate"),
            trigger: "blur",
          },
        ],
        password: [
          {
            required: true,
            message: this.$t("uc.login.pwdvalidate1"),
            trigger: "blur",
          },
          {
            type: "string",
            //min: 6,
            message: this.$t("uc.login.pwdvalidate2"),
            trigger: "blur",
          },
        ],
      },
    };
  },
  created: function () {
    this.init();
  },
  computed: {
    isLogin: function () {
      return this.$store.getters.isLogin;
    },
  },
  methods: {
    init() {
      this.$store.commit("navigate", "nav-other");
      this.$store.state.HeaderActiveName = "0";

      if (this.isLogin) {
        this.$router.push("/uc/safe");
      } else {
        //this.initGtCaptcha();
      }
    },
    onKeyup(ev) {
      if (ev.keyCode == 13) {
        $(".login_btn").click();
      }
    },
    initGtCaptcha() {
      var that = this;
      this.$http.get(this.host + this.api.uc.captcha).then(function (res) {
        window.initGeetest(
          {
            // 以下配置参数来自服务端 SDK
            gt: res.body.gt,
            challenge: res.body.challenge,
            offline: !res.body.success, //表示用户后台检测极验服务器是否宕机
            new_captcha: res.body.new_captcha, //用于宕机时表示是新验证码的宕机
            product: "bind",
            width: "100%",
          },
          this.handler
        );
      });
    },
    handler(captchaObj) {
      captchaObj
        .onReady(() => {
          $("#wait").hide();
        })
        .onSuccess(() => {
          let result = (this._captchaResult = captchaObj.getValidate());
          if (!result) {
            this.$Message.error("请完成验证");
          } else {
            this.handleSubmit("formInline");
          }
        });
      $(".login_btn").click(() => {
        // let reg = /^[1][3,4,5,6,7,8,9][0-9]{9}$/,
        // tel = this.formInline.user,
        let flagtel = this.formInline.user.length >= 4 ? true : false,
          flagpass = this.formInline.password.length >= 6 ? true : false;
        flagtel && flagpass; // && captchaObj.verify();
        (!flagtel || !flagpass) &&
          this.$Message.error(this.$t("common.fillComplete"));
        this.handleSubmit("formInline"); // 屏蔽了验证
      });
    },
    logout() {
      this.$http.post(this.host + "/uc/logout", {}).then((response) => {
        var resp = response.body;
        if (resp.code == 0) {
          localStorage.setItem("MEMBER", JSON.stringify(null));
          localStorage.setItem("TOKEN", null);
          localStorage.removeItem("USERKEY", null);
        } else {
          // this.$Message.error(resp.message);
        }
      });
    },
    login() {
      //先檢查帳號密碼邏輯
      let flagtel = this.formInline.user.length >= 4 ? true : false,
        flagpass = this.formInline.password.length >= 6 ? true : false;
      // let flagtel = true,
      //   flagpass = true;
      if (!flagtel || !flagpass) {
        this.$Message.error("請輸入正確帳號密碼");
      } else {
        login({
          input: this.formInline.user,
          password: this.formInline.password,
        })
          .then((response) => {
            if (response.code == "1") {
              this.$Message.success(this.$t("uc.login.success"));
              //this.$store.commit("setMember", this.formInline.user);
              //this.$store.commit("setMember", { user: this.formInline.user });
              //this.$store.commit("setToken", response.token);
              // if (this.$route.query.key != null && this.$route.query.key != "") {
              //   localStorage.setItem("USERKEY", this.$route.query.key);
              // }

              //this.$router.push("/uc/safe");
              this.$router.push({
                name: "SafeLogin",
                params: {
                  userId: this.formInline.user,
                  token: response.data.verification_token,
                  status: "login",
                },
              });
            } else {
              this.$Message.error(response.message);
            }
          })
          .catch((error) => {
            this.$Message.error(error);
          });
      }
    },
    handleSubmit(name) {
      // 不带验证
      this.$refs[name].validate((valid) => {
        if (valid) {
          var params = {};
          params["username"] = this.formInline.user;
          params["password"] = this.formInline.password;
        } else {
        }
      });
      /* 带验证*/
      var result = this._captchaResult;
      if (!result) {
        $("#notice").show();
        setTimeout(function () {
          $("#notice").hide();
        }, 2000);
      } else {
        this.$refs[name].validate((valid) => {
          if (valid) {
            var params = {};
            params["username"] = this.formInline.user;
            params["password"] = this.formInline.password;
            // this.$http
            //   .post(this.host + this.api.uc.login, params)
            //   .then((response) => {
            //     var resp = response.body;
            //     if (resp.code == 0) {
            //       this.$Message.success(this.$t("uc.login.success"));
            //       this.$store.commit("setMember", response.body.data);
            //       if (
            //         this.$route.query.key != null &&
            //         this.$route.query.key != ""
            //       ) {
            //         localStorage.setItem("USERKEY", this.$route.query.key);
            //       }
            //       this.$router.push("/uc/safe");
            //     } else {
            //       this.$Message.error(resp.message);
            //     }
            //   });
          } else {
          }
        });
      }
    },
  },
};
</script>

<style>
.ivu-select-single .ivu-select-selection .ivu-select-placeholder,
.ivu-select-single .ivu-select-selection .ivu-select-selected-value {
  padding-right: 20px;
}
.ivu-select-arrow {
  right: 4px;
}

.ivu-select-item span:first-child {
  display: inline-block;
  width: 30px;
  text-align: left;
}
</style>

<style scoped lang="scss">
/* 验证码 */
.login_form {
  background: #0b1520 url(../../assets/images/login_bg.png) no-repeat center
    center;
  height: 760px;
  position: relative;
  overflow: hidden;
  .login_right {
    padding: 20px 30px 20px 30px;
    position: absolute;
    background: #17212e;
    width: 350px;
    height: 330px;
    left: 50%;
    top: 50%;
    margin-left: -175px;
    margin-top: -165px;
    border-top: 4px solid #f0ac19;
    border-radius: 5px;
    form.ivu-form.ivu-form-label-right.ivu-form-inline {
      .login_title {
        height: 70px;
        color: #fff;
      }
      .ivu-form-item {
        .ivu-form-item-content {
          .login_btn.ivu-btn {
            width: 100%;
            background-color: #f0ac19;
            outline: none;
            border-color: #f0ac19;
            color: #fff;
            font-size: 18px;
            border-radius: 5px;
            &:focus {
              -moz-box-shadow: 0px 0px 0px #fff, 0px 0px 0px #fff;
              -webkit-box-shadow: 0px 0px 0px #fff, 0px 0px 0px #fff;
              box-shadow: 0px 0px 0px #fff, 0px 0px 0px #fff;
            }
          }
        }
      }
    }
  }
  .to_register {
    overflow: hidden;
    font-size: 12px;
    span {
      float: left;
    }
    a {
      float: right;
      color: #f0ac19;
    }
  }
}
#captcha {
  width: 100%;
  display: inline-block;
}
.show {
  display: block;
}
.hide {
  display: none;
}
#notice {
  color: red;
}
#wait {
  text-align: left;
  color: #666;
  margin: 0;
}
.geetest_wait_dot geetest_dot_1 {
  color: red;
}
.user .ivu-btn,
.ivu-btn:active,
.ivu-btn:focus {
  border-color: #d7dde4;
  box-shadow: none;
}
/*  */
</style>